.projects-container {
  padding: 8% 5%;
  background: linear-gradient(135deg, #74b9ff 0%, #4dabf7 100%);
  min-height: 100vh;
  width: 100%;
  color: white;
}

.projects-container h1 {
  text-align: center;
  font-size: 3rem;
  margin-bottom: 50px;
}

.project-card {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 22px;
  width: 320px;
  transition: transform 0.3s, box-shadow 0.3s;
  backdrop-filter: blur(6px);
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 16px 40px rgba(0,0,0,0.25);
}

.project-card img {
  width: 100%;
  height: 200px;
  border-radius: 12px;
  margin-bottom: 15px;
}

.project-card h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.project-card p {
  font-size: 1rem;
  margin-bottom: 15px;
  opacity: 0.9;
}

.tag {
  display: inline-block;
  background: rgba(255, 255, 255, 0.2);
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 0.9rem;
  margin-right: 5px;
  margin-bottom: 5px;
}

.project-link {
  display: inline-block;
  margin-top: 15px;
  padding: 12px 22px;
  background: #ffffff;
  color: #0d1b2a;
  text-decoration: none;
  border-radius: 24px;
  transition: all 0.3s;
}

.project-link:hover {
  background: #415a77;
  color: white;
}

