.sky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: 0;
}

.star {
  position: absolute;
  width: 3px;
  height: 3px;
  background: white;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
  animation: fall linear infinite;
  top: -10px;
  pointer-events: none;
}

@keyframes fall {
  0% {
    transform: translateY(-10px);
    opacity: 1;
  }
  100% {
    transform: translateY(100vh);
    opacity: 0;
  }
}

.container {
  position: relative;
  z-index: 1;
  display: grid;
  grid-template-columns: 50% 50%;
  align-items: center;
  justify-items: center;
  min-height: 100vh;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  color: white;
  padding: 8% 5%;
  gap: 4%;
}

.typing-container {
  text-align: left;
  margin-bottom: 0;
  width: 100%;
  max-width: 600px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.typing-text {
  font-size: clamp(3rem, 6vw, 5rem);
  font-weight: 800;
  line-height: 1.1;
  margin-bottom: 1rem;
  color: #fff;
}

@keyframes glow {
  from {
    filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.3));
  }
  to {
    filter: drop-shadow(0 0 30px rgba(255, 255, 255, 0.6));
  }
}

.bio-text {
  font-size: clamp(1.1rem, 2.5vw, 1.5rem);
  margin-bottom: 2rem;
  opacity: 0.95;
  line-height: 1.6;
}

.social-icons {
  display: flex;
  gap: 2rem;
  justify-content: flex-start;
  margin-bottom: 2.5rem;
}

.social-icons a {
  color: white;
  transition: all 0.3s ease;
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.3));
}

.social-icons a:hover {
  transform: scale(1.3) rotate(5deg);
  filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.8));
}

.button-group {
  display: flex;
  gap: 1rem;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.custom-button {
  padding: 0.8rem 2rem;
  font-size: clamp(0.9rem, 2vw, 1.1rem);
  border: 2px solid white;
  border-radius: 30px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  text-decoration: none;
  transition: all 0.3s ease;
  cursor: pointer;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.custom-button:hover {
  background: white;
  color: #667eea;
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(255, 255, 255, 0.4);
}

footer {
  text-align: center;
  padding: 2rem;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  margin-top: 3rem;
}

.content-section {
  position: relative;
  z-index: 1;
  max-width: 900px;
  margin: 0 auto;
  padding: 10% 5%;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  scroll-margin-top: 70px;
}

.content-section h2 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  margin-bottom: 1.5rem;
  font-weight: 800;
  color: #fff;
}

.content-section p {
  font-size: clamp(1.1rem, 2.5vw, 1.3rem);
  opacity: 0.95;
  line-height: 1.8;
  margin-bottom: 2rem;
  max-width: 700px;
}

.content-section a {
  color: #fff;
  text-decoration: underline;
}

.content-section a:hover {
  opacity: 0.8;
}

.section.last {
  padding-bottom: 8%;
}

.content-section .custom-button {
  margin-top: 1rem;
}

.about-section {
  position: relative;
  z-index: 1;
  max-width: 1400px;
  margin: 0 auto;
  padding: 8% 5%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  scroll-margin-top: 70px;
}

.about-content {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 5%;
  align-items: center;
  width: 100%;
}

.about-text {
  text-align: left;
}

.about-text h2 {
  font-size: clamp(2.5rem, 5vw, 4rem);
  margin-bottom: 1.5rem;
  font-weight: 800;
  color: #fff;
}

.about-text p {
  font-size: clamp(1rem, 2vw, 1.2rem);
  line-height: 1.8;
  opacity: 0.95;
  margin-bottom: 1.2rem;
}

.about-image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-image img {
  width: 100%;
  max-width: 400px;
  height: auto;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.4);
  border: 4px solid rgba(255, 255, 255, 0.2);
}

@media (max-width: 900px) {
  .container {
    grid-template-columns: 1fr;
    padding: 10% 5%;
    gap: 3rem;
  }
  .typing-container {
    text-align: center;
  }
  .social-icons {
    justify-content: center;
  }
  .button-group {
    justify-content: center;
  }
  .about-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  .about-text {
    text-align: center;
  }
  .about-image img {
    max-width: 300px;
  }
}

