
.about-container {
  padding: 8% 5%;
  max-width: 1200px;
  margin: 0 auto;
  color: white;
  min-height: 100vh;
  width: 100%;
}

.about-grid {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 5%;
  align-items: center;
}

.about-text h1 {
  font-size: clamp(2.5rem, 5vw, 4rem);
  margin-bottom: 1.5rem;
}

.about-text p {
  font-size: clamp(1rem, 2vw, 1.2rem);
  line-height: 1.8;
  opacity: 0.95;
  margin-bottom: 1.2rem;
}

.about-photo {
  display: flex;
  justify-content: center;
}

.about-photo img {
  width: 100%;
  max-width: 420px;
  border-radius: 20px;
  border: 4px solid rgba(255, 255, 255, 0.25);
}

@media (max-width: 900px) {
  .about-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .about-text {
    text-align: center;
  }
}

.about-container h1 {
  font-size: 3rem;
  margin-bottom: 30px;
}

.about-container p {
  font-size: 1.2rem;
  line-height: 1.8;
  margin-bottom: 20px;
}

